import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/views/Layout'
import Home from '@/views/Home'
import Login from '@/views/Login'

import Xinyi from '@/scripts/xianyi'
import Role from '@/views/Layout/Role'
import XyRole1 from '@/scripts/xianyi/Role1.vue'
import XyRole2 from '@/scripts/xianyi/Role2.vue'
import XyRole3 from '@/scripts/xianyi/Role3.vue'
import XyRole4 from '@/scripts/xianyi/Role4.vue'
import XyRole5 from '@/scripts/xianyi/Role5.vue'
import XyEnd from '@/scripts/xianyi/End.vue'
import XyClue from '@/scripts/xianyi/Clue.vue'
Vue.use(VueRouter)
const routes = [{
  path: '/',
  redirect: '/login'
}, {
  path: '/login',
  component: Login,
},
{
  path: '/layout',
  redirect: '/layout/home',
  component: Layout,
  children: [{
    path: 'home',
    component: Home
  }]
}, {
  path: '/xianyi',
  component: Xinyi,
}, {
  path: '/role',
  component: Role,
  children: [{
    path: 'xyclue',
    component: XyClue,
    name: 'xyClue'
  }, {
    path: 'xyrole1',
    component: XyRole1,
    name: 'xyRole1',
  }, {
    path: 'xyrole2',
    component: XyRole2,
    name: 'xyRole2',
  }, {
    path: 'xyrole3',
    component: XyRole3,
    name: 'xyRole3',
  }, {
    path: 'xyrole4',
    component: XyRole4,
    name: 'xyRole4',
  }, {
    path: 'xyrole5',
    component: XyRole5,
    name: 'xyRole5',
  }, {
    path: 'xyend',
    component: XyEnd,
    name: 'xyEnd',
  }]
}, {
  path: '/suizu',
  component: () => import('@/scripts/suizu')
},
{
  path: '/game02',
  name: 'game02',
  component: () => import('@/scripts/suizu/game')
}, {
  path: '/diaomin',
  component: () => import('@/scripts/diaomin')
}, {
  path: '/game03',
  name: 'game03',
  component: () => import('@/scripts/diaomin/game')
}]
const router = new VueRouter({
  routes
})
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}
export default router