<template>
  <div class="containe">
    <van-tabs v-model="active" sticky>
      <van-tab title="">
        <div v-if="$store.state.level > 2">
          <van-card
            :title="obj.title"
            :thumb="obj.imgUrl"
            v-for="obj in pubClue"
            :key="obj.id"
            @click="imgPreFn(obj.imgUrl)"
          >
            <template #desc>
              <div>{{ obj.desc }}</div>
            </template>
          </van-card>
        </div>
      </van-tab>
      <van-tab title="">
        <van-field
          v-model="number"
          type="number"
          label=""
          placeholder=""
          @keydown.enter="addFn(number)"
        />

        <van-collapse
          v-model="activeNames"
          v-for="obj in clueList"
          :key="obj.id"
          v-show="obj.isShow"
        >
          <van-collapse-item :title="obj.title" :name="obj.id">
            <template #title>
              <div>
                {{ obj.title }}
                <van-icon name="star" color="#ee0a24" v-show="obj.sign" />
              </div>
            </template>
            <van-card :thumb="obj.imgUrl" @click="imgPreFn(obj.imgUrl)">
              <template #desc>
                <div>{{ obj.desc }}</div>
              </template>
              <template #num>
                <van-button
                  size="small"
                  @click.stop="signFn(obj.id)"
                  :text="obj.sign ? '' : ''"
                ></van-button>
              </template>
            </van-card>
          </van-collapse-item>
        </van-collapse>
      </van-tab>
      <van-tab title="">
        <van-field
          v-model="number2"
          type="number"
          label=""
          placeholder=""
          @keydown.enter="add2Fn(number2)"
        />
        <van-collapse
          v-model="activeNames"
          v-for="obj in belongingsList"
          :key="obj.id"
          :name="obj.id"
          v-show="obj.isShow"
        >
          <van-collapse-item :title="obj.title" :name="obj.id"
            ><van-card :thumb="obj.imgUrl" @click="imgPreFn(obj.imgUrl)">
              <template #desc>
                <div>{{ obj.desc }}</div>
              </template>
              <template #num>
                <van-button
                  size="small"
                  @click.stop="delFn(obj.id)"
                  color="linear-gradient(to right, #ff6034, #ee0a24)"
                  ></van-button
                >
              </template>
            </van-card>
          </van-collapse-item>
        </van-collapse>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { ImagePreview } from "vant";
import { Toast } from "vant";

export default {
  data () {
    return {
      active: this.$store.state.active,
      activeNames: this.$store.state.activeNames,
      number: "",
      number2: "",
      pubClue: [
        {
          id: 0,
          title: "",
          desc: "",
          imgUrl: require("../../assets/xianyi/map.jpg"),
        },
        {
          id: 0,
          title: "",
          desc: ",,,",
          imgUrl: require("../../assets/xianyi/yanhuiting.jpg"),
        },
        {
          id: 1,
          title: "",
          desc: "",
          imgUrl: require("../../assets/xianyi/jisitai.jpg"),
        },
        {
          id: 2,
          title: "",
          desc: ",",
          imgUrl: require("../../assets/xianyi/xibei.jpg"),
        },
        {
          id: 3,
          title: "",
          desc: "",
          imgUrl: require("../../assets/xianyi/bei.jpg"),
        },
        {
          id: 4,
          title: "",
          desc: "",
          imgUrl: require("../../assets/xianyi/dong.jpg"),
        },
        {
          id: 5,
          title: "",
          desc: "",
          imgUrl: require("../../assets/xianyi/shenjing.jpg"),
        },
        {
          id: 6,
          title: "",
          desc: ",",
          imgUrl: require("../../assets/xianyi/beimingfangjian.jpg"),
        },
      ],
      clueList: this.$store.state.clueList,
      belongingsList: this.$store.state.belongingsList,
    };
  },
  watch: {
    activeNames () {
      this.$store.commit("activeNamesFn", this.activeNames);
    },
    active () {
      this.$store.commit("activeFn", this.active);
    },
  },

  methods: {
    imgPreFn (url) {
      ImagePreview({
        images: [url],
        showIndex: false,
      });
    },
    addFn (id) {
      const idx = this.clueList.findIndex((item) => item.id == id);
      this.$store.commit("clueListFn", this.clueList);
      if (id == 250) {
        this.clueList.forEach((obj) => (obj.isShow = true));
      } else if (this.clueList[idx] === undefined) {
        Toast.fail("");
      } else {
        this.clueList[idx].isShow = true;
        Toast.success("");
        this.$store.commit("clueListFn", this.clueList);
      }
    },
    add2Fn (id) {
      const idx = this.belongingsList.findIndex((item) => item.id == id);
      this.$store.commit("belongingsListFn", this.belongingsList);
      if (id == 250) {
        this.belongingsList.forEach((obj) => (obj.isShow = true));
      } else if (this.belongingsList[idx] === undefined) {
        Toast.fail("");
      } else {
        this.belongingsList[idx].isShow = true;
        Toast.success("");
        this.$store.commit("belongingsListFn", this.belongingsList);
      }
    },
    signFn (id) {
      const idx = this.clueList.findIndex((item) => item.id == id);
      this.clueList[idx].sign = !this.clueList[idx].sign;
      this.$store.commit("clueListFn", this.clueList);
    },
    delFn (id) {
      const idx = this.belongingsList.findIndex((item) => item.id == id);
      this.belongingsList[idx].isShow = false;
      this.$store.commit("belongingsListFn", this.belongingsList);
    },
  },
};
</script>

<style scoped>
body {
  background-color: #e8e8e8;
}
.containe {
  padding-bottom: 130px;
}
.van-card__desc {
  word-wrap: break-word;
}
.van-cell {
  border-radius: 10px 10px 0 0;
  background-color: #e8e8e8;
  color: #000;
}
.van-field {
  background-color: #fff;
  border-radius: 10px;
}
.van-collapse-item__content {
  border-radius: 0px 0px 10px 10px;
  background-color: #f7f7f7;
}
.van-card {
  border-radius: 10px;
  background-color: #f7f7f7;
  color: #000;
}
.van-card__title {
  font-size: 16px;
  margin-bottom: 5px;
}

.info_box_con {
  background: #252525;
  border-radius: 10px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: relative;
  color: #bebebe;
  font-size: 14px;
  letter-spacing: 1px;
}
.item {
  position: relative;
  margin: 0px 0;
  height: 120px;
}

.item img {
  width: 80px;
  height: 80px;
  position: absolute;
  left: 20px;
  top: 25%;
}
.item p {
  position: absolute;
  top: 15px;
  left: 110px;
  line-height: 25px;
}
.item p i {
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
}
</style>