<template>
  <div class="main">
    <van-nav-bar title="" />
    <div class="imgHeader">
      <van-image :src="imgUrl" round cover />
    </div>
    <van-form @submit="onSubmit">
      <van-field
        v-model="password"
        type="password"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"
        @focus="imgUrl = require('@/assets/1.png')"
        @blur="imgUrl = require('@/assets/0.png')"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          ></van-button
        >
      </div>
    </van-form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      password: localStorage.getItem('password') || "",
      imgUrl: require("@/assets/0.png"),
    };
  },
  methods: {
    onSubmit () {
      if (this.password == 7) {
        this.$router.replace("/layout");
      }
      localStorage.setItem('password', this.password)
    },
  },
};
</script>

<style scoped>
body {
  height: 100%;
  background-color: #fff;
}

.van-form {
  width: 100%;
  margin-top: 100px;
  text-align: center;
}
.imgHeader {
  display: relative;
}
.van-image {
  display: absolute;
  top: 50px;
  left: 50%;
  margin-left: -150px;
  width: 300px;
  height: 300px;
}
</style>