<template>
  <div>
    <router-view></router-view>
    <div>
      <van-tabbar route>
        <van-tabbar-item icon="orders-o" :to="{ name: $store.state.role }"
          ></van-tabbar-item
        >

        <van-tabbar-item icon="search" @click="showPopup"></van-tabbar-item>
        <van-button
          type="info"
          @click="nextFn"
          round
          :text="title[$store.state.level - 1]"
        ></van-button>
        <van-popup
          v-model="show"
          position="bottom"
          safe-area-inset-bottom
          round
          :style="{ height: '90%' }"
        >
          <div class="containe">
            <van-tabs v-model="active">
              <van-tab title="">
                <div v-if="$store.state.level > 2">
                  <van-card
                    :title="obj.title"
                    :thumb="obj.imgUrl"
                    v-for="obj in pubClue"
                    :key="obj.id"
                    @click="imgPreFn(obj.imgUrl)"
                  >
                    <template #desc>
                      <div>{{ obj.desc }}</div>
                    </template>
                  </van-card>
                </div>
                <div v-else><p class="text"></p></div>
              </van-tab>

              <van-tab title="">
                <van-field
                  v-model="number"
                  type="number"
                  label=""
                  placeholder=""
                  @keydown.enter="addFn(number)"
                />

                <van-collapse
                  v-model="activeNames"
                  v-for="obj in clueList"
                  :key="obj.id"
                  v-show="obj.isShow"
                >
                  <van-collapse-item :title="obj.title" :name="obj.id">
                    <template #title>
                      <div>
                        {{ obj.title }}
                        <van-icon
                          name="star"
                          color="#ee0a24"
                          v-show="obj.sign"
                        />
                      </div>
                    </template>
                    <van-card :thumb="obj.imgUrl" @click="imgPreFn(obj.imgUrl)">
                      <template #desc>
                        <div>{{ obj.desc }}</div>
                      </template>
                      <template #num>
                        <van-button
                          size="small"
                          @click.stop="signFn(obj.id)"
                          :text="obj.sign ? '' : ''"
                        ></van-button>
                      </template>
                    </van-card>
                  </van-collapse-item>
                </van-collapse>
              </van-tab>
              <van-tab title="">
                <van-field
                  v-model="number2"
                  type="number"
                  label=""
                  placeholder=""
                  @keydown.enter="add2Fn(number2)"
                />
                <van-collapse
                  v-model="activeNames"
                  v-for="obj in belongingsList"
                  :key="obj.id"
                  :name="obj.id"
                  v-show="obj.isShow"
                >
                  <van-collapse-item :title="obj.title" :name="obj.id"
                    ><van-card
                      :thumb="obj.imgUrl"
                      @click="imgPreFn(obj.imgUrl)"
                    >
                      <template #desc>
                        <div>{{ obj.desc }}</div>
                      </template>
                      <template #num>
                        <van-button
                          size="small"
                          @click.stop="delFn(obj.id)"
                          color="linear-gradient(to right, #ff6034, #ee0a24)"
                          ></van-button
                        >
                      </template>
                    </van-card>
                  </van-collapse-item>
                </van-collapse>
              </van-tab>
            </van-tabs>
          </div>
        </van-popup>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import { ImagePreview } from "vant";
import { Toast } from "vant";
import { Dialog } from "vant";
export default {
  data () {
    return {
      // active: 0,
      second: false,
      title: ["", "", ""],
      show: false,
      active: this.$store.state.active,
      activeNames: this.$store.state.activeNames,
      number: "",
      number2: "",
      pubClue: [
        {
          id: 0,
          title: "",
          desc: "",
          imgUrl: require("../../assets/xianyi/map.jpg"),
        },
        {
          id: 0,
          title: "",
          desc: ",,,",
          imgUrl: require("../../assets/xianyi/yanhuiting.jpg"),
        },
        {
          id: 1,
          title: "",
          desc: "",
          imgUrl: require("../../assets/xianyi/jisitai.jpg"),
        },
        {
          id: 2,
          title: "",
          desc: ",",
          imgUrl: require("../../assets/xianyi/xibei.jpg"),
        },
        {
          id: 3,
          title: "",
          desc: "",
          imgUrl: require("../../assets/xianyi/bei.jpg"),
        },
        {
          id: 4,
          title: "",
          desc: "",
          imgUrl: require("../../assets/xianyi/dong.jpg"),
        },
        {
          id: 5,
          title: "",
          desc: "",
          imgUrl: require("../../assets/xianyi/shenjing.jpg"),
        },
        {
          id: 6,
          title: "",
          desc: ",",
          imgUrl: require("../../assets/xianyi/beimingfangjian.jpg"),
        },
      ],
      clueList: this.$store.state.clueList,
      belongingsList: this.$store.state.belongingsList,

    };
  },
  props: ["url"],
  methods: {
    nextFn () {
      if (this.$store.state.level === 1) {
        Dialog.confirm({
          title: "",
          message: "",
        })
          .then(() => {
            this.$store.commit("levelFn");
          })
          .catch(() => {
            // on cancel
          });
      } else if (this.$store.state.level === 2) {
        Dialog.confirm({
          title: "",
          message: "",
        })
          .then(() => {
            this.$store.commit("levelFn");
          })
          .catch(() => {
            // on cancel
          });
      } else if (this.$store.state.level === 3) {
        Dialog.confirm({
          title: "",
          message: "",
        })
          .then(() => {
            // this.$store.commit("levelFn");
            this.$router.push({ name: "xyEnd" });
          })
          .catch(() => {
            // on cancel
          });
      }
    },
    showPopup () {
      this.show = true
    },
    imgPreFn (url) {
      ImagePreview({
        images: [url],
        showIndex: false,
      });
    },
    addFn (id) {
      const idx = this.clueList.findIndex((item) => item.id == id);
      this.$store.commit("clueListFn", this.clueList);
      if (id == 250) {
        this.clueList.forEach((obj) => (obj.isShow = true));
      } else if (this.clueList[idx] === undefined) {
        Toast.fail("");
      } else {
        this.clueList[idx].isShow = true;
        Toast.success("");
        this.$store.commit("clueListFn", this.clueList);
      }
    },
    add2Fn (id) {
      const idx = this.belongingsList.findIndex((item) => item.id == id);
      this.$store.commit("belongingsListFn", this.belongingsList);
      if (id == 250) {
        this.belongingsList.forEach((obj) => (obj.isShow = true));
      } else if (this.belongingsList[idx] === undefined) {
        Toast.fail("");
      } else {
        this.belongingsList[idx].isShow = true;
        Toast.success("");
        this.$store.commit("belongingsListFn", this.belongingsList);
      }
    },
    signFn (id) {
      const idx = this.clueList.findIndex((item) => item.id == id);
      this.clueList[idx].sign = !this.clueList[idx].sign;
      this.$store.commit("clueListFn", this.clueList);
    },
    delFn (id) {
      const idx = this.belongingsList.findIndex((item) => item.id == id);
      this.belongingsList[idx].isShow = false;
      this.$store.commit("belongingsListFn", this.belongingsList);
    },
  },
  watch: {
    activeNames () {
      this.$store.commit("activeNamesFn", this.activeNames);
    },
    active () {
      this.$store.commit("activeFn", this.active);
    },
  }
};
</script>

<style scoped lang="less">
body {
  padding-bottom: 50px;
}
.van-button {
  width: 300px;
  margin: 5px;
  height: 80px;
}
.van-overlay {
  height: 300px;
}
.text {
  font-size: 30px;
  padding-left: 50px;
}
</style>